.header {
  width: 100%;
  height: 3rem;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 1rem;
  background-color: #FFFFFF;
  z-index: 100;
  transition: 0.5s;
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -ms-transition: 0.5s;
  -o-transition: 0.5s;
}
.header .header_toggle {
  font-size: 1.5rem;
  cursor: pointer;
}
.header .header_img {
  width: 35px;
  height: 35px;
  display: flex;
  justify-content: center;
  border-radius: 50%;
  overflow: hidden;
}
.header .header_img .header_img img {
  width: 40px;
}

.l-navbar {
  position: fixed;
  top: 0;
  left: -30%;
  width: 68px;
  height: 100vh;
  background-color: #30323D;
  padding: 0.5rem 1rem 0 0;
  transition: 0.5s;
  z-index: 100;
}
.l-navbar .nav {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  overflow: hidden;
}
.l-navbar .nav .nav_logo {
  margin-bottom: 2rem;
}
.l-navbar .nav .nav_logo .nav_logo-icon {
  font-size: 1.25rem;
  color: #FFFFFF;
}
.l-navbar .nav .nav_logo .nav_logo-name {
  color: #FFFFFF;
  font-weight: 700;
}
.l-navbar .nav .nav_link {
  position: relative;
  color: rgba(255, 255, 255, 0.5);
  margin-bottom: 1.5rem;
  transition: 0.3s;
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  -ms-transition: 0.3s;
  -o-transition: 0.3s;
}
.l-navbar .nav .nav_link:hover {
  color: #FFFFFF;
}
.l-navbar .nav .nav_link .nav_icon {
  font-size: 1.25rem;
}

.nav_logo,
.nav_link {
  display: grid;
  grid-template-columns: max-content max-content;
  align-items: center;
  column-gap: 1rem;
  padding: 0.5rem 0 0.5rem 1.5rem;
}

.show {
  left: 0;
}

.body-pd {
  padding-left: calc(68px + 1rem);
}

.nav_link.active {
  color: #FFFFFF;
}
.nav_link.active:before {
  content: "";
  position: absolute;
  left: 0;
  width: 2px;
  height: 32px;
  background-color: #FFFFFF;
}

.height-100 {
  height: 100vh;
}

@media screen and (min-width: 768px) {
  .sidebar {
    margin: 4rem 0 0 0;
    padding-left: calc(68px + 2rem);
  }

  .header {
    height: 4rem;
    padding: 0 2rem 0 calc(68px + 2rem);
  }
  .header .header_img {
    width: 40px;
    height: 40px;
  }
  .header .header_img img {
    width: 45px;
  }

  .l-navbar {
    left: 0;
    padding: 1rem 1rem 0 0;
  }

  .show {
    width: 224px;
  }

  .body-pd {
    padding-left: 256px;
  }
}